.courses {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(270px, 1fr));
  grid-gap: 30px;
  -ms-flex-pack: center;
      justify-content: center;
}

@media (max-width: 450px) {
  .courses {
    grid-gap: 10px;
  }
}



